.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input {
  border: 2px #f4f4f4 solid;
  border-radius: 8px 0 0 8px;
  padding: 0.5em 0.7em;
  font-size: 1em;
  color: #a1a1a1;
  margin-bottom: 1rem;
}

.submit {
  background: #2759ff;
  color: white;
  border: none;
  font-size: 1em;
  padding: 0.5em 1em;
  border-radius: 0 8px 8px 0;
  cursor: pointer;
}